import { redirect } from "next/navigation"
import { useFormState } from "react-dom"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { createSupabaseServer } from "@/lib/supabase/server"

interface FormState {
  error?: string
}

async function signIn(_: FormState, formData: FormData): Promise<FormState> {
  "use server"
  
  const email = formData.get("email")?.toString() ?? ""
  const password = formData.get("password")?.toString() ?? ""
  const redirectTo = formData.get("from")?.toString() || "/admin"

  const supabase = await createSupabaseServer()
  const { error } = await supabase.auth.signInWithPassword({ email, password })
  
  if (error) {
    return { error: error.message }
  }

  redirect(redirectTo)
}

function LoginForm({ from }: { from?: string }) {
  "use client"
  
  const [state, formAction] = useFormState(signIn, {})

  return (
    <main className="py-16 md:py-24">
      <div className="container px-4 md:px-6">
        <form action={formAction} className="mx-auto flex max-w-md flex-col gap-6 rounded-xl border border-border p-6">
          <div className="space-y-2">
            <h1 className="text-2xl font-semibold">관리자 로그인</h1>
            <p className="text-sm text-muted-foreground">이메일과 비밀번호로 로그인하세요.</p>
          </div>
          <input type="hidden" name="from" value={from ?? "/admin"} />
          <div className="space-y-2">
            <label htmlFor="email" className="text-sm font-medium">
              이메일
            </label>
            <Input id="email" name="email" type="email" required placeholder="you@example.com" />
          </div>
          <div className="space-y-2">
            <label htmlFor="password" className="text-sm font-medium">
              비밀번호
            </label>
            <Input id="password" name="password" type="password" required placeholder="********" />
            {state?.error && <p className="text-sm text-destructive">{state.error}</p>}
          </div>
          <Button type="submit" className="w-full">
            로그인
          </Button>
        </form>
      </div>
    </main>
  )
}

export default function LoginPage({ searchParams }: { searchParams?: { from?: string } }) {
  const from = searchParams?.from || "/admin"
  return <LoginForm from={from} />
}

